.banner{
    background-size: cover;
    height: 36.8rem;
    color: #fff;
    margin-bottom: 3rem;
}
.content{
  padding-top: 16rem;
  height: 11rem;
  
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 3rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: rgb(0, 0, 0);
    background-color: #e6e6e6;
}

.discription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 20rem;
    max-width: 100%;
}

.fade_bottom{
    margin-top: -11rem;
    height:8rem ;
    /* background-image: linear-gradient(180deg,transparent,rgba(255, 255, 255, 0.61),rgb(235, 235, 235)); */
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}
